@import url("https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC");

.App {
	padding: 1em;
}

label {
	display: inline-block;
	width: 100px;
}

header {
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* typography */

html {
	font-family: "helvetica neue", sans-serif;
}

thead th,
tfoot th {
	font-family: "Rock Salt";
}

th {
	letter-spacing: 2px;
	padding: 10px;
}

td {
	letter-spacing: 1px;
	overflow-x: hidden;
	overflow-y: hidden;
	font-size: 12px;
	padding: 10px;
	text-align: center;
}

/* table */

table {
	table-layout: fixed;
	width: 100%;
	border-collapse: collapse;
	overflow: hidden;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

tbody tr:nth-child(odd) {
	background-color: rgba(255, 135, 135, 0.551);
}

/* Column Size for thead */
thead th:nth-child(4) {
	width: 30%;
}

thead th:nth-child(5) {
	width: 30%;
}

thead th:nth-child(-n + 3) {
	width: 10%;
}

thead th:nth-child(6) {
	width: 10%;
}

/* Column size for tbody */
td:nth-child(4) {
	width: 30%;
}

td:nth-child(5) {
	width: 30%;
}

td:nth-child(-n + 3) {
	width: 10%;
}

td:nth-child(6) {
	width: 10%;
}

.content_td p {
	max-width: 100%;
	max-height: 100px;
	overflow-y: auto;
	text-overflow: ellipsis;
}
